  border: none;
  width: min-content;
  height: min-content;
}
.button--green {
  background-color: rgba(104, 183, 56, 0.85);
  color: #ffffff;
}
.button--gray {
  background-color: #f2f2f2;
  color: #444444;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
